import React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import { css } from "@emotion/react"
import Layout from "../../components/layout"
import Seo from "../../components/seo"
import "../../styles/main-privacy.css"
import Campaign from "../../components/campaign"
import "../../styles/main-case-study.css"

export default function Home() {
  return (
    <Layout>
      <Seo title="サポート事例" />
      <div className="wrap">

        <section className="mv_cs">
          <div className="mv_cs-inner">
            <p className="txt1"><span>サポート事例</span></p>
            <p className="txt2 pc">
              株式会社ホットソウルでは、<br/>
              IT活用やデジタル化に伴う<br/>
              様々なお悩みを解決しております。
            </p>
          </div>
        </section>

        <section className="mv-lead sp">
          <div className="mv-lead-inner">
            <p className="txt2">
              株式会社ホットソウルでは、IT活用やデジタル化に伴う様々なお悩みを解決しております。
            </p>
          </div>
        </section>
          
        <section className="case">
          <div className="bg-orange case-item">
            <h2><span className="icon1">テレワークの環境整備を<br className="sp"/>相談したい</span></h2>
            <div className="case-inner">
              <div className="flex">
                <div className="flex-item left">
                  <p className="ttl-nayami">悩み</p>
                  <p>
                    テレワークの導入に向けて、必要な設備やツールをそろえたいが、何が一番良いのかわからない。
                    個人情報など業務上の秘密情報も取り扱うので、効率と安全を両立したITツールの利用を模索している。
                  </p>
                </div>
                <div className="flex-item right">
                    <StaticImage
                      src="../../images/main-case-study/item1.png"
                      alt="テレワークの環境整備を相談したい"
                      placeholder="blurred"
                    />
                </div>
              </div>
              <div className="support-box">
                <p className="ttl-support">サポート</p>
                <p>
                  近頃は、リモートで会話をするためのビデオ会議ツールやビジネスチャットを無料から利用することができ、
                  またテレワーク導入に対する補助金や助成金の制度も多く用意され、小さな会社でもテレワーク導入に取り組み易くなっています。
                  しかし、実際に導入するとなると、考える事や選択肢の多さに悩まれる経営者様も多いのではないでしょうか。
                  <br/>
                  道具選びで最も大切なことは、必要十分な、扱いやすい、丁度良いものを選ぶことだと考えます。
                  <br/>
                  業務内容はもちろんのこと、経営戦略や将来の展望、従業員の皆様の希望もうかがった上で、
                  価格と機能、便利さと安全性のバランスの丁度よい所を一緒に探りながら、総合的なITの知見を活かし、
                  貴社に最適なツール構成をご提案します。
                  またご要望があれば、実際の導入やその後の運用まで一気通貫でご支援いたします。
                </p>
              </div>
            </div>
          </div>
          <div className="case-item">
            <h2><span className="icon2">デジタル化を気軽に<br className="sp"/>相談する相手がいない</span></h2>
            <div className="case-inner">
              <div className="flex">
                <div className="flex-item left">
                  <p className="ttl-nayami">悩み</p>
                  <p>紙の帳票が多く、デジタル化を考えているが、そもそもどこから手をつけていいかわからない。社内はもちろん、社外にも気軽に相談できる相手がいない。</p>
                </div>
                <div className="flex-item right">
                  <StaticImage
                    src="../../images/main-case-study/item2.png"
                    alt="デジタル化を気軽に相談する相手がいない"
                    placeholder="blurred"
                  />
                </div>
              </div>
              <div className="support-box">
                <p className="ttl-support">サポート</p>
                <p>
                  お悩み事の解決で最も難しいのは、信頼できる相談相手を見つけること、ではないでしょうか。
                  また信頼関係というものは、一朝一夕で出来るものではありません。
                  <br/>
                  そこで弊社では、はじめにオンラインでの簡単な無料相談、その後訪問での詳細な無料相談という、２段階の無料相談の仕組みをご用意しております。
                  オンライン会議のやり方がわからない、という方には電話でのご相談も対応いたします。（要予約）
                  <br/>
                  また無料相談の後も強引な勧誘は一切いたしません。ご希望があった場合のみ、貴社に合ったサポートプランとお見積りをご提示いたします。
                  <br/>
                  中小企業診断士資格を持つIT業界20年以上のベテランエンジニアが、豊富な経験をもとにヒアリングしますので、お悩み事を頑張ってご説明頂く必要もありません。
                  普段通りにお話し頂ければ大丈夫です。
                  <br/>
                  “あなたの会社のデジタル戦略室”として、是非お気軽にご相談ください。
                </p>
              </div>
            </div>
          </div>
          <div className="bg-orange case-item">
            <h2>
              <span className="icon3">
                複数のITツールを導入済みだが、<br className="sp"/>使いこなせていない気がする
              </span>
            </h2>
            <div className="case-inner">
              <div className="flex">
                <div className="flex-item left">
                  <p className="ttl-nayami">悩み</p>
                  <p>
                    すでに複数のITツールを導入済みだが、上手く使いこなせていない感じがする。
                    もっとよい使い方があるのではないか？
                    <br/>
                    また、機能が重複しているようなITツールは使用をやめて費用削減できないか？
                  </p>
                </div>
                <div className="flex-item right">
                  <StaticImage
                    src="../../images/main-case-study/item3.png"
                    alt="複数のITツールを導入済みだが、使いこなせていない気がする"
                    placeholder="blurred"
                  />
                </div>
              </div>
              <div className="support-box">
                <p className="ttl-support">サポート</p>
                <p>
                  ITツールには、似たような機能を持つものが多くあります。
                  その中から自社に合ったものを選択するには、導入の目的を明確にすることが大切です。
                  <br/>
                  既に導入済みの場合でも、目的を再確認・再整理することで、無駄になってしまっているものの有効活用や縮小削減を進めることが出来ます。
                  <br/>
                  勧められて導入したもののよく理解しないまま使用しているものがあれば、腹落ちできるまで丁寧に解説いたします。
                  その上で、やはり不要という結論に至れば、思い切って使用をやめ、その分費用削減することも可能です。
                  <br/>
                  貴社に合った最適なITツール構成へと整えるお手伝いをいたします。
                </p>
              </div>
            </div>
          </div>
          <div className="case-item">
            <h2><span className="icon4">重要なファイルが<br className="sp"/>見つからない！</span></h2>
            <div className="case-inner">
              <div className="flex">
                <div className="flex-item left">
                  <p className="ttl-nayami">悩み</p>
                  <p>
                    社員が各々自分のやり方でファイルを管理しているので、名前や保管場所がバラバラ。
                    <br/>
                    他の社員はどこに何があるかわからないので、急な休みのときなどに引き継げず、その業務が止まってしまう。
                  </p>
                </div>
                <div className="flex-item right">
                  <StaticImage
                    src="../../images/main-case-study/item4.png"
                    alt="重要なファイルが見つからない！"
                    placeholder="blurred"
                  />
                </div>
              </div>
              <div className="support-box">
                <p className="ttl-support">サポート</p>
                <p>
                  社員それぞれが自分のやり方でファイル名をつけたり保存先を決めていると、他の社員がそのファイルを使いたい時にすぐに見つけることができません。
                  全員が無理なく守ることが出来る簡潔なルールを定め、同じルールに則ってファイルの管理を行うことが大切です。
                  <br/>
                  弊社が20年以上携わるソフトウェア産業の業界では、設計や試験、作業管理等のために数多くのファイルが作られ、
                  所属会社も異なる多数のメンバーで共有することが日常茶飯事です。
                  そこで培われた知見を活かし、貴社の業務の流れやITツールの利用状況もふまえて、属人化を防ぐためのルール（作業標準）作りを支援いたします。
                </p>
              </div>
            </div>
          </div>
          <div className="bg-orange case-item">
            <h2><span className="icon5">情報セキュリティ対策を<br className="sp"/>お願いしたい</span></h2>
            <div className="case-inner">
              <div className="flex">
                <div className="flex-item left">
                  <p className="ttl-nayami">悩み</p>
                  <p>
                    デジタル化が進むにつれて、情報漏洩が心配。
                    <br/>
                    使用しているセキュリティソフトで十分か。
                  </p>
                </div>
                <div className="flex-item right">
                  <StaticImage
                    src="../../images/main-case-study/item5.png"
                    alt="デジタル化が進むにつれて、情報漏洩が心配。"
                    placeholder="blurred"
                  />
                </div>
              </div>
              <div className="support-box">
                <p className="ttl-support">サポート</p>
                <p>
                  残念ながら情報セキュリティ対策においては、これさえやっておけば大丈夫！といったものは存在しません。
                  日々新たな攻撃の手口が現れるため、終わりのない戦いのようなものです。
                  そのため、専門の担当者を置くことが難しい小さな会社では特に悩ましい問題です。
                  そこで大切になるのが、以下の３点です。
                  <br/>
                  1.&nbsp;ウィルス対策ソフトの導入に代表されるような基本動作を徹底すること
                  <br/>
                  2.&nbsp;今あるリスクを定期的に分析・棚卸しすること
                  <br/>
                  3.&nbsp;事業の中での情報セキュリティ対策の位置づけや行動指針を全社で共有すること
                  <br/>
                  本業に専念していただきつつ、リスクの分析や情報セキュリティポリシー策定、並びに基本動作とともにポリシーを周知徹底していく
                  お手伝いをいたします。
                </p>
              </div>
            </div>
          </div>
          <div className="case-item">
            <h2><span className="icon6">補助金を活用して<br className="sp"/>ホームページを制作してほしい</span></h2>
            <div className="case-inner">
              <div className="flex">
                <div className="flex-item left">
                  <p className="ttl-nayami">悩み</p>
                  <p>ホームページが古いので、新しくしたい。<br/>できれば補助金を活用して、費用を安く抑えたい。</p>
                </div>
                <div className="flex-item right">
                  <StaticImage
                    src="../../images/main-case-study/item6.png"
                    alt="ホームページが古いので、新しくしたい。"
                    placeholder="blurred"
                  />
                </div>
              </div>
              <div className="support-box">
                <p className="ttl-support">サポート</p>
                <p>
                  今やホームページは、無料で、ご自身で簡単に作成できるツールがいくつもある時代です。
                  とにかく安くというご希望であれば、そのような無料ツールの選定から、ツールを使った実際の制作や運用までご支援できます。
                  <br/>
                  ある程度費用をかけても、しっかりしたホームページの作成をご希望の場合は、
                  経験豊富な制作チームと提携し、
                  補助金申請、ホームページの制作、インターネット上への公開、その後の運用まで一気通貫でご支援できます。
                  <br/>
                  貴社のご希望に沿って柔軟なサポートをいたしますので、お気軽にご相談ください。
                </p>
              </div>
            </div>
          </div>
        </section>

        <section className="support">
          <h2>サポート実績</h2>
            <div className="support-inner">
              <div className="flex">
                <div className="flex-item">
                  <figure>
                    <StaticImage
                      src="../../images/main-case-study/ph1.jpg"
                      alt="GO社会保険労務士法人様"
                      placeholder="blurred"
                    />
                  </figure>
                  <p className="date">
                    2021.7.21
                  </p>
                  <p className="title">
                    【IT環境 総合診断】
                    <br/>
                    GO社会保険労務士法人&nbsp;様
                  </p>
                  <p className="txt">
                    社内ITについて「これで良いのか？」とずっと不安でしたが、
                    総合的に診断して頂き、不安が消え自信が持てました。
                  </p>
                  <div className="case-study-btn">
                    <a href="#">準備中です</a>
                  </div>
                </div>
                <div className="flex-item">
                  <figure>
                    <StaticImage
                      src="../../images/main-case-study/sp-mv.png"
                      alt="アイキャッチ"
                      placeholder="blurred"
                    />
                  </figure>
                  <div className="case-study-btn">
                    <a href="#">準備中です</a>
                  </div>
                </div>
                <div className="flex-item">
                  <figure>
                    <StaticImage
                      src="../../images/main-case-study/sp-mv.png"
                      alt="アイキャッチ"
                      placeholder="blurred"
                    />
                  </figure>
                  <div className="case-study-btn">
                    <a href="#">準備中です</a>
                  </div>
                </div>
              </div>
                <p className="archive"><a href="#">過去の記事はこちら</a></p>
            </div>
        </section>

        <section className="faq">
         <h2>よくあるご質問</h2>
         <div className="faq-bg">
           <div className="faq-inner">
             <div className="item-box">
               <div className="item first">
                 <p className="q">パソコンもITも、全く苦手なのですが、大丈夫ですか？</p>
                 <p className="a">だいたいの人が苦手なのでご安心ください。そのために、私たちがいるのです。なるべく親切にわかりやすく、ご相談にのります。</p>
               </div>
               <div className="item">
                 <p className="q">うちの会社のIT環境を診てもらえますか？</p>
                 <p className="a">まずは、オンライン無料相談でお困りごとやご要望、貴社の所在地などをお伝えください。対応可能であれば、訪問診断させていただきます。</p>
               </div>
               <div className="item">
                 <p className="q">対応エリアは？</p>
                 <p className="a">神奈川県を中心に首都圏で対応しております。まずはご相談ください。</p>
               </div>
               <div className="item">
                 <p className="q">補助金や助成金の相談もできますか？</p>
                 <p className="a">はい。補助金や助成金のご相談も承っております。</p>
               </div>
             </div>
              <div className="main-cs-btn">
                <Link to={`/contact-us`}>
                  まずは、お気軽にご相談ください。
                  <br/>
                  <span>お見積り・無料相談はこちら</span>
                </Link>
              </div>
            </div>
          </div>
        </section>

        <section>
          <Campaign/>
        </section>

      </div>
    </Layout>
  ) 
}
